body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  background-color: #222D47;
  color: #242830;
}
.projectCard {
    margin: 10px 0 10px 0;
    width: 16rem;
    /* box-shadow: 0 4px 6px 0 #7081A8, 0 4px 6px 0 #7081A8; */
    background-color: #CED2DD;
}

.projectImageWrapper {
    padding: 20px 10px 5px 10px;
    background-color: #CED2DD;
}

.projectImage {
}

.projectBody {
    background-color: #CED2DD;
}

.projectTitle {
    font-weight: bold;
    font-size: larger;
}

.projectTech {
    font-style: italic;
    font-weight: lighter;
    font-size: smaller;
    padding-bottom: 10px;
}

.projectText {
    font-size: smaller;
    margin-bottom: 45px;
}

.projectLinksArea {
    position: absolute;
    bottom: 0;
    /* margin-top: 15px; */
}

.codeLink {
    color: black;
    font-weight: lighter;
    font-size: smaller;
}

.codeLink:hover {
    color: #203C82;
    font-weight: bolder;
    text-decoration: none;
}
.portfolioArea {
    margin: 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, auto));
}

.portfolioItem {
    float: left;
    margin: 0 15px 0 15px;
}

.bioCard {
    margin: 10px 10px 10px 10px;
    width: 15rem;
    background-color: #CED2DD;
    /* box-shadow: 0 4px 10px 0 #242830, 0 4px 10px 0 #242830; */
}

.bioImage {
    padding: 20px 10px 5px 10px;
    background-color: #CED2DD;
}

.bioBody {
    background-color: #CED2DD;
}

.bioTitle {
    font-weight: bold;
    font-size: large;
}

.bioContent {
    font-style: italic;
    font-weight: lighter;
    font-size: smaller;
}

.bioText {
    padding: 10px 0 0 0;
}

.bio {
    font-size: smaller;
}

.skills {
    font-size: smaller;
}

.bioLinksArea {
}

.socialLink {
    color: black;
    font-weight: lighter;
    font-size: smaller;
}

.socialLink:hover {
    color: #203C82;
    font-weight: bolder;
    text-decoration: none;
}

.socialIcon {
    height: 17px;
}
